@charset "UTF-8";
body {
  font-family: fot-tsukuardgothic-std, sans-serif;
}

body {
  background-color: #fff;
  color: #2C100F;
}

.u-desktop {
  display: none;
}
@media screen and (max-width: 767px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (max-width: 767px) {
  .u-mobile {
    display: none;
  }
}

/* リキッドレイアウト対応 */
html {
  font-size: 16px;
}
@media screen and (max-width: strip-unit(1100px)px) {
  html {
    font-size: 1.4545454545vw;
  }
}
@media screen and (max-width: 767px) {
  html {
    font-size: 16px;
  }
}
@media screen and (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}

/* pcの電話番号発信対応 */
@media screen and (max-width: 767px) {
  a[href^="tel:"] {
    pointer-events: none;
  }
}

/* ホバー */
a {
  text-decoration: none;
  color: inherit;
  transition: opacity 0.3s;
}
@media screen and (max-width: 767px) {
  a:hover {
    opacity: 0.8;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

.about {
  background-image: url(../images/common/about__bg.png);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: contain;
}

.about__inner {
  max-width: 50rem;
  padding-top: 12.5rem;
  padding-bottom: 12.5rem;
}

.about__title {
  margin-left: 1.875rem;
}

.about__text {
  font-size: 1rem;
  line-height: 1.875;
  letter-spacing: 0.03em;
  margin: 2.5rem auto 0;
  width: 34.7222222222vw;
  max-width: 43.75rem;
}

.button {
  font-size: 1.5rem;
  font-weight: 400;
  font-family: Julius Sans One, sans-serif;
  color: #333;
  position: relative;
  width: 13.75rem;
  padding: 3.125rem 3.125rem 3.125rem 6.25rem;
}

.button::before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  border: 1px solid #333;
  border-radius: 50%;
  border-right-color: transparent;
  height: 100px;
  width: 100px;
}

.button::after {
  content: "";
  position: absolute;
  top: 3rem;
  left: 2.3125rem;
  width: 11.25rem;
  height: 1.875rem;
  border: none;
  border-right: 1px solid #333;
  border-bottom: 1px solid #333;
  transform: skew(45deg);
  transition: 0.3s;
}

.button:hover::after {
  width: 17.5rem;
}
@media screen and (max-width: 767px) {
  .button:hover::after {
    width: 15.625rem;
  }
}

.circle-bg {
  background-image: url(../images/pc/FV.jpg);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  background-attachment: fixed;
}

.header {
  width: 100%;
  height: 5rem;
  background-color: rgba(255, 255, 255, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 8888;
}

.header__inner {
  display: flex;
  justify-content: space-between;
  height: inherit;
  align-items: center;
  width: inherit;
  padding: 0 20px;
}

.header__left {
  position: relative;
  z-index: 9999;
  width: 32vw;
  max-width: 9.375rem;
}

.header__logo {
  width: inherit;
}

.header__logo img {
  width: inherit;
}

.hamburger {
  display: none;
}
@media screen and (max-width: 767px) {
  .hamburger {
    display: block;
    z-index: 9999;
  }
}

@media screen and (max-width: 767px) {
  .hamburger span {
    position: relative;
    display: block;
    height: 0.125rem;
    width: 1.375rem;
    background: #2C100F;
    border-radius: 0.0625rem;
    transition: transform 0.3s ease 0s;
  }
}

.hamburger span:nth-child(1) {
  top: 0;
}

.hamburger span:nth-child(2) {
  margin: 0.25rem 0;
}

.hamburger span:nth-child(3) {
  top: 0;
}

/*OPEN時の動き*/
.hamburger.open span:nth-child(1) {
  top: 5px;
  transform: rotate(45deg);
}

.hamburger.open span:nth-child(2) {
  transform: translateY(-50%);
  opacity: 0;
}

.hamburger.open span:nth-child(3) {
  top: -7px;
  transform: rotate(-45deg);
}

/*is-OPEN時の中身*/
@keyframes open {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.drawer-menu {
  display: none;
}
@media screen and (max-width: 767px) {
  .drawer-menu {
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    display: none;
    opacity: 0;
    transition: opacity 1s ease 0s;
  }
}

/*OPEN時の動き*/
.drawer-menu.open {
  display: block;
  opacity: 1;
  background-color: #fff;
  animation: open 1s ease 0s;
}

.drawer-menu__items {
  margin: 13.0625rem auto;
}

.drawer-menu__item {
  display: block;
  text-align: center;
}
.drawer-menu__item:not(:last-child) {
  margin-bottom: 2rem;
}

.drawer-menu__item a {
  font-family: Julius Sans One, sans-serif;
  color: #2C100F;
  letter-spacing: 0.125em;
  font-size: 1rem;
  width: inherit;
  height: inherit;
}

html.is-fixed {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

/*===========================================
pc-nav
===========================================*/
.pc-nav {
  height: inherit;
  display: block;
}
@media screen and (max-width: 767px) {
  .pc-nav {
    display: none;
  }
}

.pc-nav__items {
  height: inherit;
  display: flex;
  align-items: center;
}

.pc-nav__item {
  height: inherit;
}

.pc-nav__item a {
  font-family: Julius Sans One, sans-serif;
  font-size: 1.5rem;
  letter-spacing: 0.05em;
  display: grid;
  place-items: center;
  padding: 0 1.075rem;
  position: relative;
  transition: all 0.3s ease 0s;
  color: #2C100F;
  height: inherit;
}

.pc-nav__item--contact a {
  font-family: Julius Sans One, sans-serif;
  font-size: 1.5rem;
  color: #fff;
  letter-spacing: 0.05em;
  padding: 0.5625rem 1.075rem;
  background-color: #2C100F;
  border-radius: 1.875rem;
  position: relative;
  place-items: center;
  transition: all 0.3s ease 0s;
}

.pc-nav__item a:hover::after {
  position: absolute;
  content: "";
  width: 80%;
  height: 1px;
  bottom: 8px;
  left: 50%;
  transform: translateX(-50%);
  box-sizing: content-box;
}

.pc-nav__item.pc-nav__item--white {
  margin-left: 1rem;
}

.pc-nav__item.pc-nav__item--white a {
  background-color: #fff;
  padding: 0 2rem;
  letter-spacing: 0.1em;
}

.pc-nav__item.pc-nav__item--white a:hover::after {
  display: none;
}

.inner {
  width: 100%;
  padding-right: 20px;
  padding-left: 20px;
  margin-right: auto;
  margin-left: auto;
}
@media screen and (max-width: 767px) {
  .inner {
    max-width: 1150px;
    padding-right: 25px;
    padding-left: 25px;
  }
}

.main-view {
  width: 100%;
  height: 100vh;
  position: relative;
}
@media screen and (max-width: 375px) {
  .main-view {
    background-image: url(../images/sp/sp-FV.jpg);
  }
}

.main-view__inner {
  width: 69.4444444444vw;
  max-width: 62.5rem;
}

.main-view__title {
  color: #fff;
  font-size: 4.5rem;
  font-weight: 400;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.section-title {
  font-family: Julius Sans One, sans-serif;
  font-size: 4.5rem;
  font-weight: 400;
}

.section {
  margin-top: 12.5rem;
}

.swiper {
  padding-bottom: 70px;
}

.swiper-pagination-bullet {
  background-color: #C3D384;
  width: 1.25rem;
  height: 1.25rem;
}

.swiper-pagination-bullet-active {
  background-color: #4D8532;
  width: 1.25rem;
  height: 1.25rem;
}

.swiper-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet, .swiper-pagination-horizontal.swiper-pagination-bullets .swiper-pagination-bullet {
  margin-left: 0.625rem;
}

.test {
  font-size: 1.25rem;
  background-color: #111;
  color: #fff;
}

.works-button__container {
  padding: 6.25rem;
  text-align: right;
}

.works__inner {
  max-width: 75rem;
}